Office.initialize = function (reason) { };

/**
 * Handles the OnMessageRecipientsChanged event.
 * @param {*} event The Office event object
 */
function CCInspector(event) {
  try {
    console.log("tagExternal_onMessageRecipientsChangedHandler method"); // debugging

    // Get recipients from the current email item
    // Office.context.mailbox.item.to.getAsync(function (result) {
    //   if (result.status === Office.AsyncResultStatus.Succeeded) {
    //     const toRecipients = result.value;
    //     checkRecipients(toRecipients, "To");
    //   }
    // });

    Office.context.mailbox.item.cc.getAsync(function (result) {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        const ccRecipients = result.value;
        checkRecipients(ccRecipients, "CC");
      }
    });

    // Office.context.mailbox.item.bcc.getAsync(function (result) {
    //   if (result.status === Office.AsyncResultStatus.Succeeded) {
    //     const bccRecipients = result.value;
    //     checkRecipients(bccRecipients, "BCC");
    //   }
    // });
  } catch (error) {
    console.error("An error occurred: " + error.message);

    // Display an error notification
    const errorDetails = {
      type: Office.MailboxEnums.ItemNotificationMessageType.ErrorMessage,
      message: "An error occurred: " + error.message,
      icon: "iconError",
      persistent: true
    };
    const errorNotificationId = "errorNotification";
    Office.context.mailbox.item.notificationMessages.addAsync(errorNotificationId, errorDetails);
  } finally {
    event.completed();
  }
}

// Function to check recipients and display notifications
function checkRecipients(recipients, recipientType) {
  if (recipients.length > 1) {
 
    const message = {
      type: Office.MailboxEnums.ItemNotificationMessageType.ErrorMessage,
    message: "ACHTUNG: Sie verwenden mehr als einen Adressaten im CC-Feld. Sind Sie sicher?"
     
  };

  // Show a notification message
  Office.context.mailbox.item.notificationMessages.replaceAsync("action", message);
   
  }
}

// Associate the event handler
Office.actions.associate("CCInspector", CCInspector);



